@font-face {
  font-family: 'icomoon';
  src:
    url('../fonts/icomoon.ttf?l7equz') format('truetype'),
    url('../fonts/icomoon.woff?l7equz') format('woff'),
    url('../fonts/icomoon.svg?l7equz#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

i {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

