@import 'scss/reset.css';
@import 'scss/font.css';

h1,
h2,
h3 {
  font-family: 'Oswald', sans-serif;
  letter-spacing: 3px;
}

// $accent: #d66939;
// $gray: #d1d1d1;
// $main: #121212;
// $light: #999999;

$accent: #000;
$gray: #ffffff;
$main: #121212;
$light: #f0eeee;

body,
html {
  font-size: 62.5%;
  background-color: $gray;
  font-family: 'Exo 2', sans-serif;
  color: $main;
  padding-top: 7rem;

  @media(max-width: 640px) {
    font-size: 50%;
  }
}

.container {
  max-width: 1210px;
  margin: 0 auto;
  padding: 0 2rem;
}


.button {
  text-decoration: none;
  text-align: center;
  color: inherit;
  outline: none;
  background-color: transparent;
  border: 1px solid $accent;
  max-width: 20rem;
  padding: 2rem 0;
  border-radius: .4rem;
  cursor: pointer;
  transition: .3s ease-in-out;
  margin-top: 1rem;

  &:hover {
    color: $accent;
    transform: scale(1.1);
    border: 1px solid transparent;
    box-shadow: -2px 2px 10px 0px rgba(0, 0, 0, 0.5);
  }
}

.section-title {
  position: relative;
  font-size: 2.2rem;
  margin: 2rem 0;
  text-align: right;

  &::before {
    position: absolute;
    content: '';
    height: 1px;
    width: 70%;
    background-color: $accent;
    left: 2rem;
    top: 50%;

    @media(max-width: 640px) {
      width: 30%;
    }
  }

}

;

